import React, { Component } from 'react';
import axios from 'axios';


class Blog extends Component {
    constructor(props) {
        super(props)
        this.state = {
            daftarKelurahan: [],
            nomor: '',
            kecamatan: '',
            kelurahan: '',
            kodepos: ''
        }
    }

    // get koneksi
  componentDidMount() {
    axios.get('https://kodepos-2d475.firebaseio.com/kota_kab/k69.json?print=pretty')
      .then(response => {
        // cek koneksi/response
        // console.log('response', response)
        this.setState({ daftarKelurahan: response.data })
        // console.log('response', response.data.data)
      })
      .catch(error => {
        console.log('error', error)
      })
  }

    render() {
        return (
            <div className="container">
                <h1 className="text-center" style={{ fontWeight: 'bold', marginBottom: 30 }}>Daftar Kecamatan, Kelurahan, Pos</h1>
                <div className="row">
                    <table className="table">
                        <thead className="thead-dark">
                            <tr>
                                <th scope="col">Kecamatan</th>
                                <th scope="col">Kelurahan</th>
                                <th scope="col">Pos</th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.state.daftarKelurahan.map((daftarKelurahan) => (
                            <tr key={daftarKelurahan.nomor}>
                                <th scope="row">{daftarKelurahan.kecamatan}</th>
                                <th scope="row">{daftarKelurahan.kelurahan}</th>
                                <th scope="row">{daftarKelurahan.kodepos}</th>
                            </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            </div>
        )
    }
}


export default Blog