import React, { Component } from 'react';
import axios from 'axios';


class Registration extends Component {
    constructor(props) {
        super(props)
        this.state = {
            namenya: '',
            email: '',
            addName: '',
            addEmail: '',
            buttonDisabled: false,
            formStatus: 'register',
            resultnya: ''
        }
    }

    inputOnChangeHandler = (event) => {
        this.setState({ [event.target.name]: event.target.value })
    }
    // send data
    onSubmitHandler = (e) => {
        // console.log('form di submit')
        e.preventDefault()
        // disable tombol submit
        this.setState({ buttonDisabled: true })
        // set object dengan data
        var kirimData = {
            namenya: this.state.namenya,
            email: this.state.email
        }
        var url = 'https://reqres.in/api/users'
       if (this.state.formStatus === 'register') {
            this.addMember(url, kirimData)
        }

    }

    addMember = (url, kirimData) => {
       axios.post(url, kirimData)
            .then(response => {
                this.setState({
                    buttonDisabled: false,
                    addName: this.state.namenya,
                    addEmail: this.state.email,
                    namenya: '',
                    email: '',
                    resultnya: 'berhasil register'
                })

                console.log('response', response.data)
            })
            .catch(error => {
                this.setState({
                    buttonDisabled: false,
                    resultnya: 'gagal register'
                })
                console.log('error', error)
            }) 
    }

    render() {
        return (
            <div className="container">
                <h1 className="text-center" style={{}}>Registration</h1>
                <div className="row">
                    <div className="col-md-6" style={{}}>
                        <h3 className="text-center" style={{ fontWeight: 'bold', marginBottom: 30 }}>Form</h3>
                        <form onSubmit={this.onSubmitHandler}>
                            <div className="form-group">
                                <div className="col" >
                                    <h5>Name</h5>
                                    <input type="text" className="form-control" name="namenya" value={this.state.namenya} onChange={this.inputOnChangeHandler}></input>
                                    <br />
                                    <h5>Email</h5>
                                    <input type="text" className="form-control" name="email" value={this.state.email} onChange={this.inputOnChangeHandler}></input>
                                </div>
                            </div>
                            <div className="col text-center">
                                <button type="submit" className="btn btn-danger" disabled={this.state.buttonDisabled}>Submit</button>
                            </div>
                        </form>
                        <br />
                    </div>
                    <div className="col-md-6" style={{}}>
                        <h3 className="text-center" style={{ fontWeight: 'bold', marginBottom: 30 }}>result nya</h3>
                        <div className="col">
                            <h5>Name : </h5><label style={{ fontWeight: 'bold', fontSize: 20 }}>{this.state.addName}</label>
                            <h5>Email : </h5><label style={{ fontWeight: 'bold', fontSize: 20 }}>{this.state.addEmail}</label>
                        </div>
                        <div className="col text-center" style={{}}>
                            <h2>{this.state.resultnya}</h2>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


export default Registration