import React from 'react';
import Card from '../components/Card';
import Intro from '../components/Intro';

const intro = {
    content: `Program pelatihan belajar pemrograman 
    dengan tatap muka secara offline dan 
    dengan bimbingan para instruktur profesional.
    Peserta akan belajar dengan kurikulum yang 
    berorientasi pada industri dengan teknologi
    terbaru dengan konsentrasi utama yaitu
    pemrograman web dan android. Saat ini
    program diselenggarakan di 2 kota besar,
    yaitu Bandung dan Jakarta.`
}

const cards = [
    { title: 'Weekend Bootcamp', imageUrl: 'images/koding2.jpeg', subtitle: 'Kelas belajar coding yang diselenggarakan di setiap akhir pekan, berlokasi di Bandung dan Jakarta' },
    { title: 'Intensive Bootcamp', imageUrl: 'images/koding2.jpeg', subtitle: 'Kelas belajar pemrograman Web dan Android secara insentif selama satu minggu dengan sistem karantina' },
]

const DevSchool = () => (
    <div className="container">
        <h1 style={{ textAlign: 'center' }}>DEVELOPER SCHOOL</h1>
        <div className="row" style={{ marginTop: 40, marginBottom: 100 }}>
            <Intro content={intro.content} />
            <div className="col-md-6">
                <img
                    className="img-thumbnail"
                    src="images/devschool.jpg"
                    alt=""
                />
            </div>
        </div>
        <div className="row" style={{ marginBottom: 50 }}>
            {
                cards.map(card => (
                    <Card 
                        imageUrl={card.imageUrl} 
                        title={card.title} 
                        subtitle={card.subtitle}
                    />
                ))
            }
        </div>
    </div>
)

export default DevSchool